<template>
  <el-card>
    <!---->

    <my-bread level1="" level2=""></my-bread>
    <!---->
    <el-table 
    height="400px"
    :data="rightlist"
    border
    style="width: 100%">
      <el-table-column type="index" label="#" width="100">
      </el-table-column>
      <el-table-column prop="authName" label="" width="100">
      </el-table-column>
      <el-table-column prop="path" label="" >
      </el-table-column>
      <el-table-column label="" >
          <template  slot-scope="scope">
              <span v-if="scop.row.level==='0'"></span>
              <span v-if="scop.row.level==='1'"></span>
              <span v-if="scop.row.level==='2'"></span>
          </template>
      </el-table-column>
    </el-table>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      rightlist: []
    };
  },
  created() {
    this.getRightlist();
  },
  methods: {
    async getRightlist() {
      const res = await this.$http.get("right/" + list);
      console.log(res);
      this.rightlist = res.data.data;
    }
  }
};
</script>
<style scoped>
</style>


