<template>
  <el-card class="box-card">
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-row class="searchRow">
       <el-col :span="10">
         <el-input
          @clear="loadUserList()"
          clearable
          placeholder=""
          v-model="query"
          clear
          class="inputSearch"
         >
          <el-button @click="searchUser()" slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
     <el-col :span="6">
        <el-button type="success" @click="showAddUserDia()"></el-button>
      </el-col>
    </el-row>
    <!---->
    <el-table
      height="350px"
      :data="userlist"
      style="width: 100%">
      <el-table-column label="#" width="60" type="index"></el-table-column>
      <el-table-column prop="username" label="" width="80"></el-table-column>
      <el-table-column prop="email" label=""></el-table-column>
      <el-table-column prop="mobile" label=""></el-table-column>
      <el-table-column prop="cread_time" label="">
        <!---->
        <template slot-scope="scop">{{scop.row.create_time | fmtdate}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-switch 
          @change="changeMgState(scope.row)"
          v-model="scope.row.mg_state" 
          active-color="#13ce66"
           inactive-color="#ff4949"></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <template  slot-scope="scope">
          <el-button type="primary"
           size="mini"
           plain icon="el-icon-edit" 
           @click="showEditUserDia(scope.row)"
           circle>
          </el-button>
          <el-button 
          @click="showEditUserRoleDia(scope.row)"
          type="success"
          size="mini"
          plain icon="el-icon-check"
          circle>
          </el-button>
          <!---->
          <el-button
            type="danger"
            size="mini"
            plain
            icon="el-icon-delete"
            @click="showDeleUserMsgBox(scope.row.id)"
            circle
          ></el-button>
          
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[2, 4, 6, 8]"
      :page-size="2"
      layout=" total,sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>

    <!---->
    <!---->
    <el-dialog title="" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="form">
        <el-form-item label="" label-width="100px">
          <el-input disabled v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false"> </el-button>
        <el-button type="primary" @click="addUser()"> </el-button>
      </div>
    </el-dialog>

    <!---->
     <el-dialog title="" :visible.sync="dialogFormVisibleEdit">
      <el-form :model="form">
        <el-form-item label="" label-width="100px">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleEdit = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisibleEdit=false"> </el-button>
      </div>
    </el-dialog>

    <!---->
     <el-dialog title="" :visible.sync="dialogFormVisibleRol">
  <el-form :model="form">
    <el-form-item label="" label-width="100px">
       {{"currUsername"}}
    </el-form-item>
    <el-form-item label="" label-width="100px">
<!--selectoptionvalueoptionlabel-->
            {{currRoleId}}
      <el-select v-model="currRoleId" >
             
        <el-option label="" :value="-1"></el-option>
        <el-option :label="item.roleName" :value="item.id"
        v-for="(item,i) in roles" :key="i">
          
        </el-option>
      </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisibleRol = false"> </el-button>
    <el-button type="primary" @click="dialogFormVisibleRol = false"> </el-button>
  </div>
</el-dialog>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      query: "",
      userlist: [],
      total: -1,
      pagenum: 1,
      pagesize: 2,
      dialogFormVisibleAdd: false,
      dialogFormVisibleEdit:false,
      dialogFormVisibleRol:false,
      form: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      //
      currRoleId:0,
      // currUserId:-1
      currUsername:'',
      roles:[]
    }
  },
  created() {
    this.getUserList();
  },
  methods: {
    //-
   async showEditUserRoleDia(user){
        this.currUsername= user.username;
        // 
          const res1 = await this.$http.get('roles');
          this.roles=res1.data.data
        //id
        const res = await this.$http.get('users/'+user.id);
        //id
       this.currRoleId =res.data.data.rid;
      // this.currRoleId =-1;
        this.dialogFormVisibleRol = true
       
    },
    //
   async changeMgState(user){
      // const res = await this.$http.put("users/${user.id}/state/${user.mg_state}");

      const res = await this.$http.put("users/"+user.id+"/state/"+user.mg_state)
      console.log(res)
    },
    //-
    async editUser(){
      const res =await this.$http.put("users/"+this.form.id,this.form);
      console.log(res)
    },
    //
    showEditUserDia(user){
      this.form = user;
      this.dialogFormVisibleEdit = true;

    },
      // -  config
    showDeleUserMsgBox(userId){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(async() => {
            //
            const res=await this.$http.delete("users/"+userId)
            console.log(res);
            if(res.data.meta.status===200){
            this.pagenum = 1;
                //
            this.getUserList();
                //
            this.$message({
            type: 'success',
            message: res.data.meta.msg
          })
                
            }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
     
    //
    async addUser (){
      const res = await this.$http.post("users", this.form);
      const {
        meta: { status, msg },
        data
      } = res.data;
      if (status === 201) {
        //
        this.$message.success(msg);
        //
        this.dialogFormVisibleAdd = false;
        //
        this.getUserList();
        //
        this.form = {};
      } else {
        this.$message.warning(msg);
      }
    },
    //
    showAddUserDia() {
      this.form={};
      this.dialogFormVisibleAdd = true;
    },
    //
    loadUserList() {
      this.getUserList();
    },
    //
    searchUser() {
      //
      this.getUserList();
    },
    //
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.pagesize = val;
      //
      this.pagenum = 1;
      this.getUserList();
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.pagenum = val;
      this.getUserList();
    },

    //
    async getUserList() {
      //1.created
      //methods:{getUserlist(){}}
      // ->
      //axios
      //js
      const AUTH_TOKEN = localStorage.getItem("token");
      this.$http.defaults.headers.common["Authorization"] = AUTH_TOKEN;
      //   console.log(res);
      //
      const res = await this.$http.get(
        //
        "users?query=" +
          this.query +
          "&pagenum=" +
          this.pagenum +
          "&pagesize=" +
          this.pagesize

        // "users?query=${this.query}&pagenum=${this.pagenum}&pagesize=${this.pagesize}"
      );

      console.log(res);
      const {
        meta: { status, msg },
      } = res.data;
      if (status === 200) {
        const {
        data: { users, total }
      } = res.data;
        //
        this.userlist = users;
        //tatal
        this.total = total;
        //
        this.$message.success(msg);
      } else {
        //
        this.$message.warning(msg);
      }
    }
  }
};
</script>
<style scoped>
.box-card {
  height: 100%;
}
.inputSearch {
  min-width: 30%;
}
.searchRow {
  margin-top: 20px;
}
</style>


