<template>
  <div class="login-warp">
    <el-form class="login-from" label-position="top" label-width="80px" :model="formdata">
      <h2></h2>
      <el-form-item label="">
        <el-input  v-model="formdata.username"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input type="password"  v-model="formdata.password" ></el-input>
      </el-form-item>
      <el-button class="login-btn" @click.prevent="handleLogin()" type="primary"></el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formdata: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    async handleLogin() {
      //    this.$http.post('login',this.formdata).then((res)=>{
      
      const res = await this.$http.post("login", this.formdata);
      //dataconstdata
      //res.datametamsg
      const {
        data, //meta ,
        meta: { msg, status }
      } = res.data;
     
      if (status === 200) {
        //
        //token
        
        localStorage.setItem("token", data.token);
         
        //home
        this.$router.push({ name: "home" });
        //
        this.$message.success(msg);
      } else {
        this.$message.warning(msg);
      }
    }
  }
};
</script>

<style scoped>
.login-warp {
  height: 100%;
  background-color: rgb(70, 83, 83);
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-warp .login-from {
  width: 30%;
  min-width: 300px;
  background-color: aliceblue;
  border-radius: 5px;
  padding: 30px;
}
.login-warp .login-btn {
  width: 100%;
}
</style>


